import axios from 'axios';
import PageHelper from '@/utils/pageHelper';
import config from '@/config';
import moment from 'moment';

const requestFormat = (payload) => {
  payload["start_at"] = payload["date_range"][0];
  payload["end_at"] = payload["date_range"][1];
  if ((payload["categories"] && payload["categories"].length > 0) || (payload["brands"] && payload["brands"].length > 0)) {
    payload["condition"] = {
      categories: payload["categories"] && payload["categories"].length > 0 ? payload["categories"] : null,
      brands: payload["brands"] && payload["brands"].length > 0 ? payload["brands"] : null,
    }
  } else payload["condition"] = null
  return payload;
}

export const getList_DISCOUNT = async (payload) => {
  const { data } = await axios.get('/discount', {params: PageHelper.requestFormat(payload)});
  data.data.map((item) => {
    item["date_range"] = [moment(item["start_at"]),moment(item["end_at"])]
    if (!!item["condition"]) {
      for(let key in item["condition"]) {
        item[key] = item["condition"][key];
      }
    }
    return item;
  })
  return PageHelper.responseFormat(data);
}
export const save_DISCOUNT = async (payload) => {
  payload = requestFormat(payload);
  const { data } = await axios.post('/discount', payload);
  if (data.message) config.notice.success(data.message);
  return data;
}
export const update_DISCOUNT = async (payload) => {
  payload = requestFormat(payload);
  const { data } = await axios.put('/discount/' + payload.id, payload);
  if (data.message) config.notice.success(data.message);
  return data;
}
export const delete_DISCOUNT = async (payload) => {
  for (let index = 0; index < payload.length; index++) {
    const id = payload[index];
    const {data} = await axios.delete('/discount/' + id);
    if (data.message) config.notice.success(data.message);
  }
  return null;
}
export const patch_DISCOUNT = async (payload) => {
  for (let index = 0; index < payload.length; index++) {
    const {data} = await axios.patch('/discount/' + payload[index].id, payload[index]);
    if (data.message) config.notice.success(data.message);
  }
  return null;
}
